@font-face {
  font-family: "Asap";
  src: url("../fonts/Asap-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Asap";
  src: url("../fonts/Asap-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Asap";
  src: url("../fonts/Asap-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Asap";
  src: url("../fonts/Asap-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Century Gothic";
  src: url("../fonts/CenturyGothic.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Century Gothic";
  src: url("../fonts/CenturyGothic-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Asap", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #151515;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 64px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  width: 100%;
  padding: 20px 0;
}
@media (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 30;
  }
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__wrapper {
  display: contents;
}
@media (max-width: 767px) {
  .header__wrapper {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 64px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    padding: 40px;
    padding-bottom: 60px;
    background-color: #151515;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    overflow: auto;
  }
  .is-open .header__wrapper {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
  .header__wrapper .header__link-enter {
    transition: opacity 0.3s ease, transform 0.3s ease;
    opacity: 0;
    transform: translateY(25px);
  }
  .is-open .header__wrapper .header__link-enter {
    opacity: 1;
    transform: translateY(0);
  }
}
.header__wrapper a {
  color: #D9D9D9;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__wrapper a:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__wrapper a.focus-visible:focus {
  opacity: 0.7;
}
.header__nav {
  margin: 0 40px;
}
@media (max-width: 767px) {
  .header__nav {
    margin: 0 auto;
  }
  .header__nav .header__item {
    transition: opacity 0.3s ease, transform 0.3s ease;
    opacity: 0;
    transform: translateY(-25px);
  }
  .is-open .header__nav .header__item {
    opacity: 1;
    transform: translateY(0);
  }
}
.header__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 40px;
  row-gap: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  line-height: 114%;
}
@media (max-width: 767px) {
  .header__list {
    flex-direction: column;
    gap: 20px;
  }
}
.header__link-enter {
  font-size: 16px;
  line-height: 114%;
}
@media (max-width: 767px) {
  .header__link-enter {
    margin: auto auto 0 auto;
  }
}
.header__burger {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__burger:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__burger.focus-visible:focus {
  opacity: 0.7;
}
.header__burger span {
  width: 24px;
  height: 2px;
  background-color: #ffffff;
  transition: transform 0.6s ease, opacity 0.3s ease, background-color 0.3s ease;
}
.is-open .header__burger span:nth-child(1) {
  transform: translateY(8px) rotate(225deg);
}
.is-open .header__burger span:nth-child(3) {
  transform: translateY(-8px) rotate(-225deg);
}
.is-open .header__burger span:nth-child(2) {
  opacity: 0;
}
@media (max-width: 767px) {
  .header__burger {
    display: flex;
  }
}

